:root {
  --primary-color: #6d6875;
  --secondary-color: #b5838d;
  --dark: #e5989b;
  --white: #ffcdb2;
  --light: #ffb4a2;
  --purple: #393b989c;
  --gradient: linear-gradient(
    to bottom right,
    hsla(12, 100%, 82%, 1) 25%,
    hsla(358, 60%, 75%, 1) 55%,
    hsla(348, 25%, 61%, 1) 83%
  );
  --gradient-transparent: linear-gradient(
    to bottom right,
    hsla(12, 100%, 82%, 0.7) 25%,
    hsla(358, 60%, 75%, 0.7) 55%,
    hsla(348, 25%, 61%, 0.7) 83%
  );
}

body {
  margin: 0;
  font-family: 'Sora', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

* {
  box-sizing: border-box;
}

html,
body {
  background-color: #f5f5f5;
  text-decoration: none;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar {
  width: 8px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #6d6875;
  background-color: var(--primary-color);
}

.section {
  min-height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
}

.primary-heading {
  text-align: center;
  margin: 0;
  font-size: 45px;
  font-weight: 400;
  letter-spacing: 1px;
  word-spacing: 2px;
  text-transform: uppercase;
  color: #6d6875;
  color: var(--primary-color);
}

.secondary-heading {
  font-weight: 400;
  font-size: 32.5px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #6d6875;
  color: var(--primary-color);
  margin: 0;
  padding-bottom: 15px;
}

.primary-para {
  font-size: 18px;
  line-height: 30px;
  color: #b5838d;
  color: var(--secondary-color);
  margin: 0;
}

.secondary-para {
  font-size: 15px;
  line-height: 20px;
}

.wrapper {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}
/*********************************************************************************************************************/

