/* Button - btn */

.btn {
  background-color: #393b989c;
  background-color: var(--purple);
  width: 50%;
  height: 100%;
  padding: 15px 20px;
  font-weight: 700;
  cursor: pointer;
  text-align: center;
  transition: 0.2s all;
  border-radius: 500px;
  color: #fafafa;
}

.btn:hover {
  box-shadow: 4px 4px 10px 0 #393b989c;
  box-shadow: 4px 4px 10px 0 var(--purple);
  background-color: #393b98cb;
}

.btn-group {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  width: 100%;
  align-items: center;
}

.btn:active {
  box-shadow: none;
}

/*******************************************************************************************/

.floating-bar {
  background: #fafafa;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  position: fixed;
  top: 35%;
  left: 0;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 20px;
  grid-gap: 16px;
  gap: 16px;
  transition: 0.3s all;
  opacity: 1;
  box-shadow: 0 8px 8px 0 #21212130;
  z-index: 10000;
}

.floating-bar > * {
  cursor: pointer;
}

.floating-bar .hide-click {
  cursor: pointer;
}

