}
.row-cont {
    display: flex;
}

.contact-divider {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 50px;
    -webkit-column-gap: 50px;
            column-gap: 50px;
    max-width: 1200px;
    margin: 0 auto;
}

.contact-left img {
    max-width: 100%;
}

.row {
    display: flex;
    flex-direction: column;
    /* margin: 0 auto; */
    /* padding: 60px 30px; */
    position: relative;
    z-index: 1;
    text-align: center;
}
.row:before {
    position: absolute;
    content: '';
    display: block;
    top: 0;
    left: -500rem;
    height: 100%;
    width: 1500rem;
    z-index: -1;
    background: inherit;
}
.row:first-child {
    padding: 10px 0;
}
.row span {
    position: relative;
    display: inline-block;
    margin: 15px 0;
}

.clean-slide {
    position: relative;
    display: inline-block;
    width: 420px;
    padding: 10px 0 1rem 15px;
    /* font-family: "Open Sans", sans; */
    font-weight: 400;
    color: var(--secondary-color);
    background: #efefef;
    border: 0;
    border-radius: 3px;
    outline: 0;
    text-indent: 60px;
    transition: all 0.3s ease-in-out;
}
.clean-slide::-webkit-input-placeholder {
    color: #efefef;
    text-indent: 0;
    font-weight: 300;
}
.clean-slide + label {
    display: inline-block;
    position: absolute;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    top: 0;
    left: 0;
    bottom: 0;
    padding: 13px 15px;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    color: #262626;
    text-align: left;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.4);
    transition: all 0.3s ease-in-out, color 0.3s ease-out;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    overflow: hidden;
}
.clean-slide + label:after {
    content: '';
    position: absolute;
    top: 0;
    right: 100%;
    bottom: 0;
    width: 100%;
    background: var(--secondary-color);
    z-index: -1;
    -webkit-transform: translate(0);
            transform: translate(0);
    transition: all 0.3s ease-in-out;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}

.clean-slide:focus,
.clean-slide:active {
    color: var(--secondary-color);
    text-indent: 0;
    background: #fff;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.clean-slide:focus::-webkit-input-placeholder,
.clean-slide:active::-webkit-input-placeholder {
    color: #aaa;
}
.clean-slide:focus + label,
.clean-slide:active + label {
    color: #fff;
    text-shadow: 0 0.1rem 0 rgba(19, 74, 70, 0.4);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.clean-slide:focus + label:after,
.clean-slide:active + label:after {
    -webkit-transform: translate(100%);
            transform: translate(100%);
}

@-webkit-keyframes pulse {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }
    33% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(3);
                transform: scale(3);
        opacity: 0;
    }
}

@keyframes pulse {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }
    33% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(3);
                transform: scale(3);
        opacity: 0;
    }
}
.button {
    display: inline-flex;
    align-items: center;
    background: var(--secondary-color);
    box-shadow: 0 0.3rem 0.2rem 0 rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    height: 45px;
    padding: 0 30px;
    color: #fff;
    font-size: 15px;
    text-transform: uppercase;
    text-decoration: none;
    transition: background 0.3s, box-shadow 0.3s, -webkit-transform 0.3s;
    transition: background 0.3s, transform 0.3s, box-shadow 0.3s;
    transition: background 0.3s, transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s;
    will-change: transform;
}
.button:hover {
    background: var(--primary-color);
    box-shadow: 0 40px 17px rgba(0, 0, 0, 0.2);
    -webkit-transform: translate3d(0, -2px, 0);
            transform: translate3d(0, -2px, 0);
}
.button:active {
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
    -webkit-transform: translate3d(0, 1px, 0);
            transform: translate3d(0, 1px, 0);
}

.pulse {
    position: relative;
}
.pulse:before,
.pulse:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.4);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    opacity: 0;
    margin: auto;
}
.pulse:before {
    -webkit-animation: pulse 1.5s infinite linear;
            animation: pulse 1.5s infinite linear;
}
.pulse:after {
    -webkit-animation: pulse 2s 0.4s infinite linear;
            animation: pulse 2s 0.4s infinite linear;
}
.pulse:hover:before,
.pulse:hover:after {
    display: none;
}

