:root {
  --black: #212121;
  --nav-back: #ffb4a275;
}

a {
  color: currentColor;
  text-decoration: none;
}

nav {
  height: 80px;
  width: 100%;
  position: fixed;
  top: 0;
  padding: 16px;
  font-size: 18px;
  z-index: 3000;
  transition: all 0.2s ease;
}

.colorBar {
  background-color: #ffb4a275;
  background-color: var(--nav-back);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
}

nav .wrapper {
  display: flex;
  align-items: center;
  height: 100%;
}

.logo {
  margin-right: auto;
  height: 70;
  width: 70;
}

nav ul {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding: 0;
  grid-gap: 15px;
  gap: 15px;
}

.links {
  font-weight: 600;
  color: #212121;
  color: var(--black);
}

.linkItem {
  position: relative;
}

.linkItem:hover {
  color: var(--primary-color);
}

.linkItem::after {
  content: '';
  height: 3px;
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 0%;
  transition: 0.2s;
  background-color: var(--primary-color);
}

.linkItem:hover::after {
